import React, {useState}  from 'react'
import RequestItem from './RequestItem';
import { makeStyles } from '@material-ui/core/styles';
import { Button, FormControl, FormControlLabel, InputLabel, MenuItem, Select, TextField } from '@material-ui/core';
import Checkbox from '@material-ui/core/Checkbox';

// style
const useStyles = makeStyles((theme) => ({
    menu:{
      backgroundColor: "rgba(206, 17, 38, 0.05)",
      height: 150,
      paddingTop: 10,
      marginBottom: 10,
      maxWidth: "600px",
      margin: "0 auto",
    },
    menulist:{
      textAlign: "center",
    },
    buttonlist:{
      textAlign: "right",
      marginTop: 10,
      marginRight: 20,
    },
    button:{
      marginRight: 15,
      backgroundColor: "white",
    },
    button2:{
      backgroundColor: "white",
    },
    formControl: {
      marginLeft: 15,
      minWidth: 150,
    },
    formControl2: {
      minWidth: 150,
    },
    check:{
      marginLeft: 5,
    },
    checkzone:{
      width: "350px",
      margin: "0 auto",
    },
}));

const Filter = ({request, history, myId}) => {
    const classes = useStyles();
    // dateValue
    const [dateValue, setDateValue] = useState('');
    // dbdate
    const [date, setDate] = useState('');
    const [language, setLan] = useState('');
    const [checked, setChecked] = useState(false);
    // filterdata
    const [filtered, setFiltered] = useState(request);

  //filter
  const filterOn = ()=>{ 
    setFiltered(
      request.filter( (item) =>{
        // checkedtrue
        if(checked === true && item.emergency !== 1)
        {return false;}
        // dateValue
        if(dateValue && item.es_date !== date)
        {return false;}
        // language
        if(language && item.lan_1  !== language)
        if(language && item.lan_2 !== language)
        if(language && item.lan_3 !== language)
        {return false;}
        return item;
      }
)
    ) 
  };

  
  // filter
  const filterOff = ()=>{
    setChecked(false);
    setDateValue("");
    setLan("");
    // 
    setFiltered(request);
  }
  
  // 
  const checkhandleChange = (e) => {
    setChecked(e.target.checked);
  };
  // 
  const handleDateChange=(e)=>{
    // -/
    var str = e.target.value;
    var newstr = str.replace(/-/g, "/");
    setDateValue(e.target.value);
    setDate(newstr);
  };
  //
    const handleChange = (e) => {
      setLan(e.target.value);
    };
    return (
        <>
          {/*  */}
          <div className={classes.menu}> 
          {/* checkbox */}
          <div className={classes.checkzone}>
          <FormControlLabel
            control={<Checkbox checked={checked} onChange={checkhandleChange} color="primary"/>}
            label=""
            className={classes.check}
          />
          </div>
          <div className={classes.menulist}>
          {/*  */}
            <FormControl>
              <TextField
                label=""
                type="date"
                value={dateValue}
                className={classes.formControl2}
                onChange={handleDateChange}
                InputLabelProps={{
                  shrink: true,
                }}
              />
            </FormControl>
            <FormControl className={classes.formControl}>
              <InputLabel shrink id="demo-simple-select-placeholder-label-label">
                
              </InputLabel>
              <Select
                labelId="demo-simple-select-placeholder-label-label"
                id="demo-simple-select-placeholder-label"
                value={language}
                onChange={handleChange}
                displayEmpty
              >
                <MenuItem value="">
                  <em></em>
                </MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
              </Select>
            </FormControl>
            </div>
            <div className={classes.buttonlist}>
              <Button variant="outlined" color="primary" className={classes.button} onClick={filterOn}></Button>
              <Button variant="outlined" color="inherit" className={classes.button2} onClick={filterOff}></Button>
            </div>
            </div>

                {/* request*/}
                { filtered && (
                <>
                {/* requestrequestItem */}
                {filtered.map((requestItem) => (
                    // requestItem
                    <RequestItem
                    key={requestItem.id}
                    id={requestItem.id}
                    picture={requestItem.picture}
                    nickname= {requestItem.nickname}
                    es_date= {requestItem.es_date}
                    start_time= {requestItem.start_time}
                    end_time= {requestItem.end_time}
                    departure= {requestItem.departure}
                    destination={requestItem.destination}
                    number={requestItem.number}
                    lan_1={requestItem.lan_1}
                    lan_2={requestItem.lan_2}
                    lan_3={requestItem.lan_3}
                    status= {requestItem.status}
                    emergency={requestItem.emergency}
                    history={history}
                    myId={myId}
                    />
                ))}
                </>
                )}
        </>            
    )
}

export default Filter
