import React, { useState } from 'react'
import { Backdrop, Button, Fade, FormControl, Modal, TextField, Typography } from '@material-ui/core';
import ModalItem from "../../common/ModalItem";
import { makeStyles } from '@material-ui/core/styles';

// style
const useStyles = makeStyles((theme) => ({
      modal: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
      },
      button: {
        display: "block",
        margin: "0 auto",
        marginBottom: 30,
    }
  }));

const Done = ({id, volunteer_id, myId}) => {
    const classes = useStyles();

    const [evaluate, setEvaluate] = useState("");
    const [comment, setComment] = useState("");

    const [open, setOpen] = useState(false);
    //modal
    const handleClose = () => {
        setOpen(false);
    };
    const handleOpen = () => {
        setOpen(true);
      };
    
    // 
    const onSubmit = () => {
        // 
        // (1)escortsstatus_2""
        // (2)volnickname
        // (3)evaluates
        setOpen(false);
    };
    
    return (
        <>
            <Button variant="contained" color="secondary" size="large" onClick={handleOpen} className={classes.button}>
                
            </Button>

            {/* modal */}
            <Modal
              aria-labelledby="transition-modal-title"
              aria-describedby="transition-modal-description"
              className={classes.modal}
              open={open}
              onClose={handleClose}
              closeAfterTransition
              BackdropComponent={Backdrop}
              BackdropProps={{
              timeout: 500,
              }}
            >
            <Fade in={open}>
                <ModalItem
                    title=""
                    text={
                        <div>
                            <Typography variant="subtitle1" display="block">
                            1~5
                            </Typography>
                            <FormControl>
                                <TextField
                                    type="number"
                                    value={evaluate}
                                    onChange={(e) => setEvaluate(e.target.value)}
                                />
                            </FormControl>
                            <Typography variant="subtitle1" display="block">
                            
                            </Typography>
                            <FormControl>
                            <TextField
                                value={comment}
                                multiline
                                rows={5}
                                variant="outlined"
                                margin="normal"
                                //  
                                onChange={(e) => setComment(e.target.value)}
                            />
                            </FormControl>

                        </div>
                    }
                    buttonText=""
                    handleClose={handleClose}
                    onSubmit={onSubmit}
                    />
                    
            </Fade>
            </Modal>
        </>
    )
}

export default Done
