import React,{useState} from "react";
import { withRouter } from 'react-router-dom';
import Title from './common/Title'
import { makeStyles } from '@material-ui/core/styles';
import { Box, Button, FormControl, TextField} from '@material-ui/core';

// id
    // id
    const volunteerId= "aaa";
    const guestId= "zzz";

// style
const useStyles = makeStyles((theme) => ({
  margin:{
      marginBottom: 150,
  },
  button:{
    display: "block",
    margin: "0 auto",
    marginTop: 10,
    marginBottom: 10,
  },
  text:{
    textAlign: "center",
  },
  input:{
    display:"block",
    width: 260,
    margin: "0 auto",
    textAlign: "center",
  },
  link:{
    display: "block",
    width: 65,
    margin: "0 auto",
    marginTop: 20,
  },
}));

const App = (props) => {
  const classes = useStyles();
  const [email, setEmail] = useState(''); 
  const [password, setPassword] = useState(''); 

      //
      const gotoGuest=()=>{
        props.history.push({
            // detailid
            pathname: '/guest',
            state: { myId: guestId }
        });
    }

    // 
    const gotoVolunteer=()=>{
        props.history.push({
            //vulnteer
            pathname: '/volunteer',
            state: {myId: volunteerId}
        })
    }

    //
    const handleSubmit=(e)=>{
      e.preventDefault();


    };   

  return (
    <>
      <Title title=""/>
      <div className={classes.margin}></div>
      <form onSubmit={handleSubmit}>
            <FormControl component="fieldset" className={classes.input}>
              <TextField
                label=""
                type="email"
                required
                value={email}
                onChange={e => setEmail(e.target.value)}
                InputLabelProps={{
                  shrink: true,
                }}
                disabled
              />
            </FormControl><br/>
            <FormControl component="fieldset" className={classes.input}>
              <TextField
                label=""
                type="password"
                required
                value={password}
                onChange={e => setPassword(e.target.value)}
                InputLabelProps={{
                  shrink: true,
                }}
                disabled
              />
            </FormControl>   
            <Button variant="contained" color="primary" className={classes.button} type="submit" disabled></Button>
          </form>  

        <Button variant="contained" color="primary" size="large" onClick={gotoVolunteer} className={classes.button}>
          
        </Button>
        <p className={classes.text}>or</p>
        <Button variant="contained" color="secondary" size="large" onClick={gotoGuest} className={classes.button}>
          
        </Button>
        <a href="/register" className={classes.link}></a>
    </>
  );
};

export default withRouter(App);
