import React from 'react'
import Title from '../../common/Title';
import GuestNoticeItem from './GuestNoticeItem';
import List from '@material-ui/core/List';
import { makeStyles } from '@material-ui/core/styles';

// 
// noticesuser_id=myID

const notices =[
    {
     id: 0,
     escorts_id: 1,
     user_id: "zzz",
     notice:"Hiroko",
     date: "2021/07/25",
     already_read: 0,
     icon:0,
    },
    {
     id: 1,
     escorts_id: 2,
     user_id: "zzz",
     notice:"Hiroko",
     date: "2021/07/24",
     already_read: 1,
     icon:2,
    },
    {
     id: 2,
     escorts_id: 3,
     user_id: "zzz",
     notice:"Hiroko",
     date: "2021/07/23",
     already_read: 1,
     icon:3,
    },
];

// style
const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        maxWidth: '500px',
        backgroundColor: theme.palette.background.paper,
        margin: "0 auto",
      },
    margin:{
        marginBottom: 60,
    },
  }));

const GuestNotice = ({myId, history}) => {
    const classes = useStyles();
    
    // dbuser_idmyId
    const filtered = notices.filter((filtereditem) => (filtereditem.user_id === myId));

    return (
        <>
        <Title title=""/>
        <div className={classes.margin}></div>   
        <List className={classes.root}>
        {/* filtereditem */}
        {filtered.map((item) => (
            // requestItem
            <GuestNoticeItem
                key={item.id}
                id={item.id}
                escorts_id={item.escorts_id}
                notice={item.notice}
                date={item.date}
                already_read={item.already_read ===0 ? ("colored"):("")}
                icon={item.icon}
                history={history}
                myId={myId}
            />
            ))}
        </List>          
        </>
    )
}

export default GuestNotice